<script lang="ts">
  import "./styles.css";
  import { IdentifoApi } from "../identifo.js";

  import router from "page";
  import LoginWrapper from "./components/LoginWrapper.svelte";
  import routes from "./routes";
  import app from "./main";

  let page;
  let params;
  let user = null;

  var urlParams = new URLSearchParams(window.location.search);
  const app_id = urlParams.get("app_id");

  IdentifoApi.configure({
    baseURL: location.origin,
    appID: app_id,
  });

  if (document.getElementsByTagName("base")[0]) {
    const baseHref = `${document
      .getElementsByTagName("base")[0]
      .getAttribute("href")
      .replace(/\/$/, "")}`;
    console.log(`set base ${baseHref}`);
    router.base(baseHref);
  }
  routes.forEach((route) => {
    router(
      route.path,

      // Set the params variable to the context.
      // We use this on the component initialisation
      (ctx, next) => {
        params = ctx.params;
        next();
      },

      // Check if auth is valid. If so, set the page to the component
      // otherwise redirect to login.
      () => {
        page = route.component;
      }
    );
  });
  router.start();
</script>

<svelte:head>
  <link
    href="https://cdn.jsdelivr.net/npm/bootstrap@5.0.0-beta3/dist/css/bootstrap.min.css"
    rel="stylesheet"
    integrity="sha384-eOJMYsd53ii+scO/bJGFsiCZc+5NDVN2yr8+0RDqr0Ql0h+rP48ckxlpbzKgwra6"
    crossorigin="anonymous"
  />
  <style
    src="https://cdn.jsdelivr.net/npm/bootstrap@5.0.0-beta3/dist/js/bootstrap.bundle.min.js"
    integrity="sha384-JEW9xMcG8R+pH31jmWH6WWP0WintQrMb4s7ZOdauHnUtxwoG2vI5DkLtS3qm9Ekf"
    crossorigin="anonymous"></style>

  <style>
    .bd-placeholder-img {
      font-size: 1.125rem;
      text-anchor: middle;
      -webkit-user-select: none;
      -moz-user-select: none;
      user-select: none;
    }

    @media (min-width: 768px) {
      .bd-placeholder-img-lg {
        font-size: 3.5rem;
      }
    }
  </style>
</svelte:head>

<div class="text-center w-100">
  <svelte:component this={page} {params} />
</div>
